import { FC } from 'react'
import { Link } from 'react-router-dom'
import logo from '../static/logo.svg'

export const IndexPage: FC = () => {
  return (
    <>
      <header>
        <div className='logo-container'>
          <Link to={'/'}>
            <img src={logo} alt='logo' className='logo' />
            <span>   </span>
          </Link>
        </div>
        <nav>
          <ul>
            <li>
              <Link to={'/'}> </Link>
            </li>
            <li>
              <Link to={'/'}></Link>
            </li>
            <li>
              <Link to={'/'}></Link>
            </li>
            <li>
              <Link to={'/'}>  </Link>
            </li>
          </ul>
        </nav>
        <div className='quote'>
                ,   ; 
              !
        </div>
      </header>
      <Link to={'/archive/'}></Link>
    </>
  )
}
